.points-counter,
.life-counter {
  color         : #15f419;
  position      : absolute;
  font-family   : monospace;
  text-transform: uppercase;
  width         : fit-content;
  font-size     : 2rem;
  padding       : 10px;
  z-index       : 99;
}

.life-counter {
  right: 0;
}

.glass {
  width    : 100%;
  height   : 100%;
  position : absolute;
  font-size: 0px;
  z-index  : 10;
  top      : 0px;
}

.pause-info {
  width          : 100%;
  height         : 100%;
  display        : flex;
  flex-direction : column;
  justify-content: center;
  align-items    : center;
  color          : #15f419;
  text-transform : uppercase;
  gap            : 12px;
}

.menu-button {
  padding: 8px;
  border : none;
}